@import "../variables";


.swiper-slide {
	text-align: center;
	font-size: 18px;
	background: #fff;
	height: 300px;
	/* Center slide text vertically */
	display: -webkit-box;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	-webkit-justify-content: center;
	justify-content: center;
	-webkit-box-align: center;
	-ms-flex-align: center;
	-webkit-align-items: center;
	align-items: center;
}

.by-testimonial {
	padding: 120px 0;
}
